import {
  Button,
  Container,
  Group,
  Modal,
  Paper,
  PasswordInput,
  Text,
  TextInput,
  Title,
} from '@mantine/core';
import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { signUpNewUser } from '../services/auth.service';

function Signup() {
  const navigate = useNavigate();
  const session = useSelector((state) => state.sessions);

  const [showCheckEmailMessage, setShowCheckEmailMessage] = useState(false);

  const [passwordError, setPasswordError] = useState('');
  const [passwordCheckError, setPasswordCheckError] = useState('');

  if (session) navigate('/');

  const clearErrors = () => {
    setPasswordError('');
    setPasswordCheckError('');
  };

  return (
    <Container mt="lg" size={500}>
      <Title align="center" mb={'xl'} order={2}>
        Luo kyttj
      </Title>
      <Paper shadow="sm" withBorder p={'xl'}>
        <form
          onSubmit={async (e) => {
            e.preventDefault();
            if (e.target.password.value.length < 8) {
              setPasswordError('Salasanan oltava vhintn 8 merkki');
            } else if (
              e.target.password.value !== e.target.passwordcheck.value
            ) {
              setPasswordCheckError('Salasanat eivt tsm');
            } else {
              const signUpConfirm = await signUpNewUser(
                e.target.email.value,
                e.target.password.value
              );
              setShowCheckEmailMessage(signUpConfirm);
            }
          }}
        >
          <TextInput label="Shkposti" name="email" required />
          {/* TODO shkposti validation */}

          <PasswordInput
            mt={'sm'}
            onChange={clearErrors}
            error={passwordError}
            label="Salasana"
            name="password"
            required
          />
          {/* TODO salasana vaatimukset */}
          <PasswordInput
            mt={'sm'}
            error={passwordCheckError}
            onChange={clearErrors}
            label="Salasana uudelleen"
            name="passwordcheck"
            required
          />
          <Group position="right">
            <Button mt={'md'} type="submit">
              Luo kyttj
            </Button>
          </Group>
        </form>
      </Paper>
      <Modal
        centered
        closeOnEscape={false}
        closeOnClickOutside={false}
        withCloseButton={false}
        opened={showCheckEmailMessage}
      >
        <Title align="center" order={2}>
          Kyttjn luonti lhetetty
        </Title>
        <Text mb={'lg'} mt={'md'} align="center">Tarkista shkpostisi</Text>
        <Link to='/'><Button fullWidth>Takaisin etusivulle</Button></Link>
      </Modal>
    </Container>
  );
}

export default Signup;
